import React from 'react';
import { AppBar, Button, Card, CardActions, Grid, Container, CardMedia,CardContent, Toolbar, CssBaseline, Typography } from '@material-ui/core';
import { PhotoCamera } from '@material-ui/icons';
import useStyles from './style';
import Footer from './components/Footer';

const cards = [1,2,3,4,5,6,7,8,9]
const App = ()=> {
    const classes = useStyles();
    return(
        <>
            <CssBaseline/>
            <AppBar position="relative">
                <Toolbar>
                    <PhotoCamera className={classes.icon}/>
                    <Typography variant="h6">Photo Album</Typography>
                </Toolbar>
            </AppBar>
            <main>
                <div className={classes.container}>
                    <Container maxWidth="sm">
                        <Typography variant="h2" align="center" color="textPrimary" gutterBottom>
                            Photo Album
                        </Typography>
                        <Typography variant="h6" align="center" color="textSecondary" paragraph>
                            Lorem, ipsum dolor sit amet consectetur adipisicing elit. Quis rerum nobis quia 
                            magnam, fugiat natus harum optio repellendus, animi ad itaque praesentium quod 
                            laboriosam nihil totam quidem incidunt ratione maxime.
                        </Typography>
                        <div className={classes.buttons}>
                            <Grid container spacing={2} justifyContent="center">
                                <Grid item>
                                    <Button variant="contained" color="primary">
                                        View Photos
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="outlined" color="primary">
                                        View Photos
                                    </Button>
                                </Grid>
                            </Grid>
                        </div>
                    </Container>
                </div>
                <Container className={classes.cardGrid} maxWidth={'md'}>
                    <Grid container spacing={4}>
                        {cards.map((card) => (
                            <Grid item key={card} xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia
                                    className={classes.cardMedia}
                                    image='https://source.unsplash.com/random'
                                    title='Image Tittle'
                                />
                                <CardContent className={classes.CardContent}>
                                    <Typography variant="h5" gutterBottom>
                                        Heading
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary">
                                        This is a media card section, use it to describe the content
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small" color="primary">Share</Button>
                                    <Button size="small" color="primary">Learn More</Button>
                                </CardActions>
                            </Card>
                        </Grid>
                        ))}
                    </Grid>
                </Container>
            </main>
            <Footer classes={classes}/>
        </>
    );
}
export default App;